.header {
  font-size: 22px;
  color: #69c9ca;
  font-family: 'Poiret One';
  text-transform: uppercase;
  margin-bottom: 30px;
  text-align: center;
}
.img {
  display: block;
  height: 150px;
  width: auto;
  margin: 0 auto;
}
@media(min-width: 1200px) {
  .header {
    font-size: 48px;
    margin-bottom: 44px;
  }
  .img {
    height: 204px;
  }
}
