.list {
  padding: 0;
  display: flex;
  list-style: none;
  align-items: center;
  height: 15px;
  transform: translate(0, 210px);
}
.listItem:not(:last-child) {
  margin-right: 20px;
}
.button {
  border: none;
  background: none;
  width: 15px;
  height: 15px;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.button:hover, .button:focus, .button:active {
  outline: none !important;
}
.span {
  display: block;
  width: 10px;
  height: 10px;
  border: 1px solid #69c9ca;
  border-radius: 50%;
}
.active {
  composes: span;
  width: 15px;
  height: 15px;
  background: #69c9ca;
}
@media(min-width: 1200px) {
  .button {
    width: 20px;
    height: 20px;
  }
  .span {
    width: 15px;
    height: 15px;
  }
  .active {
    width: 20px;
    height: 20px;
  }
  .list {
    height: 20px;
    transform: translate(0, 222px);
  }
}
