@media(min-width: 1200px) {
  .button {
    border: 0;
    width: 60px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: none;
    padding: 0;
  }
  .button:hover, .button:focus, .button:active {
    outline: none !important;
  }
  .button i {
    font-size: 60px;
    color: #fff;
    line-height: 1;
    opacity: 0.6;
    transition: opacity 0.3s ease;
  }
  .button i:hover {
    opacity: 1;
  }
}
