.section {
  height: 690px;
  background: #222222;
  background-image: url('../../img/background/eventsMobWhite.png'), url('../../img/background/eventsMobColor.png');
  background-image: -webkit-image-set(url('../../img/background/eventsMobWhite.webp') 1x, url('../../img/background/eventsMobColor.webp') 1x, url('../../img/background/eventsMobWhite.png') 1x, url('../../img/background/eventsMobColor.png') 1x);
  background-position: 0% 5%, 100% 40%;
  background-size: 36% auto, 28% auto;
  background-repeat: no-repeat;
  clip-path: polygon(0% 0%, 50% 3%, 100% 0%, 100% 97%, 50% 100%, 0 97%);
  transform: translate(0, -33%);
  -ms-transform: translate(0, 0);
  padding-top: 40px;
}
.slider {
  margin-bottom: 50px;
}
.header {
  font-size: 30px;
  color: #fff;
  text-transform: uppercase;
  font-family: 'Poiret One';
  text-align: center;
  line-height: 1;
  margin-bottom: 30px;
}
.text {
  font-size: 12px;
  color: #fff;
  text-align: center;
  font-weight: 300;
  padding: 0 15px;
}
.buttonOrder {
  margin: 0 auto 30px auto;
  line-height: 1;
  border: 1px solid #69c9ca;
  background: none;
  text-align: center;
  padding: 14px 0;
  color: #69c9ca;
  font-size: 14px;
  font-weight: 300;
  display: block;
  text-transform: uppercase;
}

@media(min-width: 1200px) {
  .section {
    background-image: url('../../img/background/eventsDeskWhite.png'), url('../../img/background/eventsDeskColorRight.png'), url('../../img/background/eventsDeskColotBotLeft.png');
    background-image: -webkit-image-set(url('../../img/background/eventsDeskWhite.webp') 1x, url('../../img/background/eventsDeskColorRight.webp') 1x, url('../../img/background/eventsDeskColotBotLeft.webp') 1x, url('../../img/background/eventsDeskWhite.png') 1x, url('../../img/background/eventsDeskColorRight.png') 1x, url('../../img/background/eventsDeskColotBotLeft.png') 1x);
    background-position: 0 10%, 100% 5%, 0% 90%;
    background-size: auto 20%, auto 38%, auto 40%;
    background-repeat: no-repeat;
    height: 886px;
    clip-path: polygon(0% 0%, 50% 6%, 100% 0%, 100% 94%, 50% 100%, 0 94%);
    transform: translate(0, -66%);
    -ms-transform: translate(0, 0);
    padding-top: 94px;
    position: relative;
  }
  .header {
    font-size: 60px;
    height: 84px;
    line-height: 84px;
    background: url('../../img/elements/buttonsGroup.png');
    background-image: -webkit-image-set(url('../../img/elements/buttonsGroup.webp') 1x);
    background-repeat: no-repeat;
    background-position: 40% 0;
    margin-bottom: 19px;
  }
  .buttonOrder {
    font-size: 16px;
    padding: 18px 0;
    border: 2px solid #69c9ca;
    transition: all 0.3s ease;
    widows: 200px;
  }
  .buttonOrder:hover {
    color: #fff;
    background: #69c9ca;
  }
  .text {
    font-size: 18px;
    width: 50%;
    margin: 0 auto;
  }
  .slider {
    margin-bottom: 36px;
  }
}
